*{
    margin: 0;
    padding: 0;
}
:root{
    --navColor:rgb(247, 73, 233);
}
#root{ 
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
}

.tituloMain{
    color: blueviolet;
    text-align: center;
    margin: 25px;
}

ul{
    list-style: none;
}

.catalogo{
    display: flex;
    
}
.contenedorTarjetas{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
}
.tarjetas{
    background-color: var(--navColor);
    margin: 10px;
    width: 250px;
    height: 250px;
    border: 1px solid black;
    position: relative;

}
.cardImgContainer{
    overflow: hidden;
    height:200px;
}
.tarjetas-img{
    width:100%;
}
.logo{
    text-align: center;
    /* font-family: 'French Script MT', sans-serif; */
    /* font-size: 50px; */
    background-color: var(--navColor);
}
.logo a{
    color: navy;
    text-decoration: none;

}
.logoInitial{
    width: 100%;
    height: 61px;

}
.logoContainer{
    display: flex;
    justify-content: center;
    position: relative;
    top: 5px;
}

.detallesCard{
    margin-left: 2px;
    color: black;
}
.tarjetas a{
    text-decoration: none;
}

.searchContainer{
    text-align: center;
}
#buscador{
    text-align: center;
    border: 2px black solid;
    margin-top: 15px;
    padding: 3px;
}

.main{
    text-align: center;
    /* margin-top: 30px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.main h2{
    color: black;
}
.provContainer{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    /* width: 95%; */
    font-size: 25px;
}
.provContainer a{
    text-decoration: none;
}
.provincias{
    background-color: plum;
    width: 200px;
    height: 200px;
    margin-top: 15px;

    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    margin-left: 5px;
}
.provincias:hover{
    cursor: pointer;
}
.provincias a{
    font-size: 50px;
    width: 100%;
    display: block;
}
.terminosContainer{
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100vw;
    background-color: plum;
    display: none;

}
.terminosContainer2{
    /* width: 80%; */
    height: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.terminos{
    /* width: 88%; */
    margin: 0 10% 0 10%;
    text-align: center;
    /* background-color: red; */
}
.btnTerminos{
    margin: 5px;
    font-size: 18px;
}
.btnTerminos a{
    text-decoration: none;
    color: black;
}
.btnCierre{
    background-color: silver;
    font-size: 20px;
    position: relative;
    top: 30px;
    text-align: center;
    z-index: 20;
    height: 27px;
}
.btnCierre:hover{
    background-color: white;
    cursor: pointer;
}
.videoContainer{
    text-align: center;
    position: relative;
    top: -27px;

}
#seleccion{
    color: violet;
}

.parrafoPrincipal{
    margin: 0 20px;
}





@media only screen and (min-width: 500px){
    .main{
        display: block;
    }
    .logoInitial{
        position: initial;
    }
    /* .provContainer{
        flex-direction: row;
        justify-content: center;
    } */
    .tarjetas{
        width: 200px;
    }

    .main h2{
        margin-top: 20px;
    }
}


