

.contenedorUserDetail{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    margin-top: 15px;
    width: 100vw;
    align-items: center;
}
.videoDetail{
    max-width: 90vw;
    max-height: 97vh;
}
.imgDetail{
    max-width: 90vw;
    /* max-height: 100vh; */
    margin-top: 15px;
    border: 1px solid black;

}
.imgContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.contenedorDetail{
    margin: 10px;
    line-height: 1.7;
    width: 80vw;

}
.marco{
    position: relative;
    text-align: center;
    margin-top: 20px;
}
.marcaDeAgua{
    opacity: 0.1;
    position: absolute;
    bottom: 50%;
    left: 20%;
}
@media only screen and (min-width: 500px){

    .contenedorUserDetail{
        display: flex;
        flex-flow: column wrap;
        width: 100%;
        justify-content: space-evenly;
        align-items: center;
        margin: 20px;
        font-size: large;
    }
    .imgDetail{
        /* width: 55vw; */
        margin-top: 45px;
    }
    .contenedorDetail{
        margin: 40px;
        line-height: 1.7;
        /* width: 500px; */
    }
    .marcaDeAgua{
        opacity: 0.08;
        position: absolute;
        bottom: 45%;
        left: 35%;
        width: 35%;
    }
}